import React, { Component } from "react";
import { withRouter } from "react-router-dom";
import {
    Button,
    Modal,
    // ModalHeader,
    ModalBody,
    ModalFooter,
    // InputGroup,
    // InputGroupAddon,
    // InputGroupText,
    // Input,
} from "reactstrap";
// import Form from './Form';
import SweetAlert from "react-bootstrap-sweetalert";
import { getValueFromEvent } from "../util/clientUtils";
import TextField from '@material-ui/core/TextField';

class Intro extends Component {
  constructor(props) {
    super(props);
    this.state = {
        modal: false,
        show: false,
        name: "",
        country: "",
        address: "",
        phone: "",
        email: "",
        quantity: 1,
        error: false,
    };
  }

  openModal = () => {
    this.setState((prevState) => ({
      modal: !prevState.modal,
    }));
  };

  closeModal = () => {
    this.setState(() => ({
        modal: false,
        show: false,
        name: "",
        country: "",
        address: "",
        phone: "",
        email: "",
        quantity: 1,
        error: false,
    }));
  };
  CheckCart(ev) {
    if (this.state.name !== "" &&
        this.state.country !== "" &&
        this.state.address !== "" &&
        this.state.phone !== "" &&
        this.state.email !== "") {
        ev.preventDefault();
        const form = ev.target;
        const data = new FormData(form);
        const xhr = new XMLHttpRequest();
        xhr.open(form.method, form.action);
        xhr.setRequestHeader("Accept", "application/json");
        xhr.onreadystatechange = () => {
            if (xhr.readyState !== XMLHttpRequest.DONE) return;
            if (xhr.status === 200) {
                form.reset();
                this.setState((prevState) => ({
                    show: !prevState.show,
                }));
            }
        };
        xhr.send(data);
    } else {
        this.setState(() => ({
            error: true,
        }));
        document.getElementById("user-validation").textContent =
            "Please fill out empty fields!";
    }
  }

  decrease = () => {
    if (this.state.quantity > 1) {
      this.setState({quantity: this.state.quantity - 1});
      document.getElementById("price").innerText = (this.state.quantity - 1) * 299 +'$'
    }
  };

  increase = () => {
      this.setState({ quantity: this.state.quantity + 1 });
      document.getElementById("price").innerText = (this.state.quantity + 1) * 299 +'$'
  };

  onInputChangedName = (e) => {
    document.getElementById("user-validation").textContent="";
    const newValues = getValueFromEvent(e);
    this.setState({
      name: newValues.name,
    });
  };
    onInputChangedCountry = (e) => {
        document.getElementById("user-validation").textContent="";
        const newValues = getValueFromEvent(e);
        this.setState({
            country: newValues.country,
        });
    };
    onInputChangedAddress = (e) => {
        document.getElementById("user-validation").textContent="";
        const newValues = getValueFromEvent(e);
        this.setState({
            address: newValues.address,
        });
    };
    onInputChangedPhone = (e) => {
        document.getElementById("user-validation").textContent="";
        const newValues = getValueFromEvent(e);
        this.setState({
            phone: newValues.phone,
        });
    };
    onInputChangedEmail = (e) => {
        document.getElementById("user-validation").textContent="";
        const newValues = getValueFromEvent(e);
        this.setState({
            email: newValues.email,
        });
    };

  render() {
      return (
      <React.Fragment>
        <SweetAlert
          success
          show={this.state.show}
          title="Congratulations!"
          onConfirm={() => {this.closeModal()}}
        >
          Your order has been submitted, We will contact you soon.
        </SweetAlert>
        <div>
          <Modal
            isOpen={this.state.modal}
            toggle={() => this.openModal()}
            className="iotModal"
          >
            <form noValidate autoComplete="off"
                  onSubmit={this.CheckCart}
                  action="https://formspree.io/mayjzzap"
                  method="post">
            <ModalBody>
              <div>
                <div className="product-pic-modal">
                  <img src="./assets/img/product.png" alt="product pic" />
                </div>
                <table className="table table-image">
                  <thead>
                    <tr>
                      <th scope="col" style={{ color: "black" }}>
                        Product
                      </th>
                      <th scope="col" style={{ color: "black" }}>
                        Price
                      </th>
                      <th scope="col" style={{ color: "black" }}>
                        Quantity
                      </th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td style={{ color: "black" }}>IoT Analytical Unit</td>
                      <td id="price" style={{ color: "black" }}>299$</td>
                      <td>
                        <div className="number-input">
                          <button onClick={this.decrease} className="minus" type="button"/>
                          <input className="quantity" name="quantity" value={this.state.quantity} type="number"/>
                          <button onClick={this.increase} className="plus" type="button"/>
                        </div>
                      </td>
                    </tr>
                  </tbody>
                </table>

                <div id="user-validation"/>
                <div className="order-form">
                    <TextField required id="standard-basic" label="Name" name="name"
                               onChange={(e) => this.onInputChangedName(e)}
                               className="text-field name"
                               error={this.state.error && this.state.name === ""}/>
                    <TextField required id="standard-basic" label="Country" name="country"
                               helperText="* Free shipping to UAE, Oman, Iran, and Kuwait is now available"
                               onChange={(e) => this.onInputChangedCountry(e)}
                               className="text-field country"
                               error={this.state.error && this.state.country === ""}/>
                    <TextField required id="standard-basic" label="Address" name="address"
                               onChange={(e) => this.onInputChangedAddress(e)}
                               className="text-field address"
                               error={this.state.error && this.state.address === ""}
                               style={{width: '80%'}}/>
                    <TextField required id="standard-basic" label="Phone" name="phone"
                               onChange={(e) => this.onInputChangedPhone(e)}
                               className="text-field phone"
                               error={this.state.error && this.state.phone === ""}/>
                    <TextField required id="standard-basic" label="Email" name="email" type="email"
                               onChange={(e) => this.onInputChangedEmail(e)}
                               className="text-field email"
                               error={this.state.error && this.state.email === ""}/>
                </div>
              </div>
              <div className="button-div">

              </div>
            </ModalBody>
            <ModalFooter>
                <Button type="submit" className="btn-custom-iot">
                    Submit
                </Button>
                <Button type="button" className="btn-custom-cancel" onClick = {() => this.closeModal()}>
                    Cancel
                </Button>
            </ModalFooter>
          </form>
          </Modal>
        </div>
        <section id="introw" className="clearfix">
          <div className="container">
            <div className="intro-img">
              <img
                id="intro-img-1"
                src="assets/img/smarthome_large.png"
                alt="intro"
              />
            </div>

            <div className="intro-info">
              <h2>Analytics Solution for Smart Homes</h2>
              <div>
                <button
                  className="btn-get-started animated infinite pulse"
                  onClick={() => this.openModal()}
                >
                  Order Now!
                </button>
                <button className="btn-services" onClick={() => this.props.history.push(`/Dashboard`)}>
                  Demo
                </button>
              </div>
            </div>
          </div>
        </section>
        <section id="intro-bottom" className="clearfix"/>
      </React.Fragment>
    );
  }
}

export default withRouter(Intro);
